.pricingSection {
    font-family: system-ui, Helvetica, sans-serif;
    color: gold;
    padding-left: 5vw;
    padding-right: 5vw;
    margin-top: 5vh;
}

.pricingHeader {
    font-family: fantasy;
}

.bestValueInfo {
    text-align: left;
}

.indent {
    padding-left: 1rem;
}

.numberList {
  margin-bottom: 1rem;
}

.goldReadingsArticle {
    margin-top: 12vh;

}

.bestValueText {

}

.priceCard {
    background-color: black;
    border: 2px solid gold;
    border-radius: 1em;
    padding: .5em;
    margin-bottom: 1vh;
}

.priceCardHeader {
    display: flex;
    align-items: center;
    margin-top: 0;
    margin-bottom: .5em;
}

.priceCardHeader h2 {
    margin: 0 1em 0 0;
    color: white;
}
.priceValue {
    color: lightgreen;
    font-size: 24px;
}

.pricingHeaderWrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

i.price {
    color: green;
}

button.showMore {
    color: gold;
    border: 1px solid gold;
}

figure.showMoreFigure {
    display: flex;
    align-items: center;
    margin-left: 0;
    color: gold;
}

.expandIcon {
    fill: gold;
    background-color: gold;
}
