.buttonLinkPrimary {
    display: inline-block;
    color: black;
    background-color: gold;
    border: 3px solid gold;
    text-decoration: none;
    padding: 1em;
    border-radius: 1em;
    margin-right: 1em;
}

.buttonLinkPrimary:visited {
    color: black;
    background-color: gold;
}

.buttonLinkSecondary {
    display: inline-block;
    color: white;
    background-color: black;
    border: 3px solid gold;
    text-decoration: none;
    padding: .5em;
    border-radius: 1em;
    margin-right: 1em;
}

.buttonLinkSecondary:visited {
    color: lightgray;
}

.topSectionSpacer {
    height: 8vh;
}

.iconButton {
    border: none;
    outline:none;
    background-color: transparent;
}