@font-face {
  font-family: 'MyWebFont';
  src: url('../../fonts/VESPASIAN_TIMES.ttf')
  format('truetype');
}

:root {
  max-width: 640px;
  margin: 0 auto 0 auto;
  --home-logo-height: 250px;
}

.home {
  padding-top: 1em;
  height: 65vh;
  min-height: 343px;
}

.logoWrapper {
  display: flex;
  justify-content: center;
  height: var(--home-logo-height);
}

.staticLogo {
  height: var(--home-logo-height);
}

.staticLogoWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.logoAlign {
  position: absolute;
  height: var(--home-logo-height);
}

.logo {
  opacity: 0;
  animation: logoIntro 4.27s linear, fadeIn 1s linear 3.27s forwards;
}

.lightLogo {
  animation: logoIntro 4.27s linear, fadeOut 1s linear 3.27s forwards;
}

.logoTextAnimation {
  margin-top: 4px;
  animation: fadeIn linear 1s forwards;
  animation-delay: 3.27s;
  opacity: 0;
}

.logoTextStyles {
  margin-top: 4px;
  font-family: MyWebFont;
  text-align: center;
  color: gold;
  font-size: 63px;
  text-transform: lowercase;
  line-height: .5;
}

.headerText {
  color: gold;
}

.preLoadText {
  color: gold;
  text-align: center;
  font-size: 32px;
  visibility: visible;
  opacity: 1;
  transition: opacity 2s linear;
}

.preLoadTextHidden {
  transition: all 1s ease-in;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 2s, opacity 2s linear;
}

.nav {
  display: flex;
  justify-content: center;
  height: 6vh;
  background: black;
  opacity: .8;
}

/* Reduce nav font size for very small width phones such as the Galaxy Fold. */
@media only screen and (max-width: 300px) {
  .nav {
    font-size: 15px;
  }
}

.pricingNav {
  height: 6vh;
  background: black;
  opacity: .8;
  text-align: center;
}

.navItem {
  color: lightgrey;
  margin-right: 1vw;
  margin-left: 1vw;
}

.animationSection {
  height: 70vh;
}

.linkSection {
  display: flex;
  justify-content: center;
  margin-bottom: 2vh;
}

@keyframes fadeOut {
  0% {
    opacity:1;
  }
  100% {
    opacity:0;
  }
}

@keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

@keyframes logoIntro {
  0% {
    transform: scale(0) rotate(0deg);
  }
  33% {
    transform: scale(.363) rotate(2880deg);
  }
  66% {
    transform: scale(.678) rotate(3960deg);
  }
  100% {
    transform: scale(1) rotate(4320deg);
  }
}
