.whySection {
    font-family: system-ui, Helvetica, sans-serif;
    color: gold;
    min-height: 36vh;
    padding-left: 5vw;
    padding-right: 5vw;
}
.whySectionWrapper {
    overflow-y: hidden;
}

.whyHeader {
    font-family: fantasy;
}

.loadMoreButton {
    margin-top: 2vh;
    margin-left: -1em;
    padding-left: 0;
}

.loadMoreButton img {
    height: 4em;
}

.expandWrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.loadMoreButtonBottom {
    display: none;
}